<template>
  <div>
    <el-breadcrumb separator="/">
      <!--  -->
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="rolelist" border stripe>
        <!--  -->
        <el-table-column type="expand" label=" ">
          <template v-slot="s">
            <el-row
              :class="['bdbottom', i1===0?'bdtop':'', 'vcenter']"
              v-for="(item1, i1) in s.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(s.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for -->
                <el-row
                  :class="[i2===0?'':'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(s.row, item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(s.row, item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template v-slot="s">
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showSetRightDialog(s.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightDialogVisible"
      width="50%"
      @close="setRightDialogClosed"
    >
      <el-tree
        ref="treeRef"
        show-checkbox
        :data="rightslist"
        :props="treeProps"
        :default-expand-all="true"
        node-key="id"
        :default-checked-keys="defKeys"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allowRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      rolelist: [],
      // 
      rightslist: [],
      // 
      setRightDialogVisible: false,
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // Id
      defKeys: [],
      // id
      roleId: ''
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    async getRolesList() {
      const { data: res } = await this.$http.get('/api/private/v1/roles')
      if (res.meta.status !== 200) {
        return this.$message('')
      }
      this.rolelist = res.data
      // console.log(this.rolelist)
    },
    async removeRightById(role, RightId) {
      const confirmResult = await this.$confirm(
        '',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      console.log('')
      const { data: res } = await this.$http.delete(
        `/api/private/v1/roles/${role.id}/rights/${RightId}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      role.children = res.data
    },
    async showSetRightDialog(role) {
      this.roleId = role.id
      this.setRightDialogVisible = true
      const { data: res } = await this.$http.get('/api/private/v1/rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // data
      this.rightslist = res.data
      this.getLeafKeys(role, this.defKeys)
      // console.log(this.rightslist)
    },
    // id
    getLeafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id) // 
      }
      node.children.forEach(element => {
        this.getLeafKeys(element, arr)
      })
    },
    setRightDialogClosed() {
      this.defKeys = []
    },
    // 
    async allowRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const {
        data: res
      } = await this.$http.post(`/api/private/v1/roles/${this.roleId}/rights`, {
        rids: idStr
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('');
      this.getRolesList();
      this.setRightDialogVisible = false;
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
