<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert show-icon title="!" :closable="false" type="warning"></el-alert>
      <!--  -->
      <el-row style="margin: 15px 0">
        <el-col>
          <span></span>
          <!--  -->
          <el-cascader
            :options="catelist"
            :props="{expandTrigger:'hover',value:'cat_id', label:'cat_name', children:'children'}"
            v-model="selectedCateKeys"
            @change="handleChange"
            clearable
            change-on-select
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tab -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button :disabled="isBtnDisabled" type="primary" @click="addDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <el-table-column type="expand">
              <template v-slot="scope">
                <!-- tag -->
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable
                @close="handleClose(i, scope.row)">{{item}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template v-slot="scope">
                <el-button
                  type="primary"
                  size="mini"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                ></el-button>
                <el-button
                  type="danger"
                  size="mini"
                  icon="el-icon-delete"
                  @click="removeParams(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button type="primary" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="onlyTableData" border stripe>
            <el-table-column type="expand">
              <template v-slot="scope">
                <!-- tag -->
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable
                @close="handleClose(i, scope.row)">{{item}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template v-slot="scope">
                <el-button
                  type="primary"
                  size="mini"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                ></el-button>
                <el-button
                  type="danger"
                  size="mini"
                  icon="el-icon-delete"
                  @click="removeParams(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog
      :title="titleText"
      :visible.sync="addDialogVisible"
      width="30%"
      @close="addDialogClosed"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>

    <el-dialog
      :title="''+titleText"
      :visible.sync="editDialogVisible"
      width="30%"
      @close="editDialogClosed"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      catelist: [],
      // 
      selectedCateKeys: [],
      // 
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // 
      editDialogVisible: false,
      // 
      inputVisible: false,
      // 
      inputValue: '',
      // 
      addForm: {},
      // 
      editForm: {},
      // 
      addFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    // 
    this.getCateList()
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get('/api/private/v1/categories')
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      this.catelist = res.data
      console.log(this.catelist)
    },
    handleChange() {
      this.getParamsData();
    },
    async getParamsData() {
      // 
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = [];
        this.manyTableData = [];
        this.onlyTableData = [];
        return
      }
      // ID
      const {
        data: res
      } = await this.$http.get(
        `/api/private/v1/categories/${this.cateId}/attributes`,
        { params: { sel: this.activeName } }
      )
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 
        item.inputVisible = false
        item.inputValue = ''
      })
      console.log(res.data)
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    handleTabClick() {
      this.getParamsData()
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // /
    addParams() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return
        }
        // console.log(this.addForm.attr_name, this.activeName)
        const {
          data: res
        } = await this.$http.post(
          `/api/private/v1/categories/${this.cateId}/attributes`,
          { attr_name: this.addForm.attr_name, attr_sel: this.activeName }
        )
        console.log(res.meta.status)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.addDialogVisible = false
        this.getParamsData()
      })
    },
    // 
    async showEditDialog(id) {
      const {
        data: res
      } = await this.$http.get(
        `/api/private/v1/categories/${this.cateId}/attributes/${id}`,
        { attr_sel: this.activeName }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
      this.editDialogVisible = true
    },
    editDialogClosed() {
      // 
      this.$refs.editFormRef.resetFields()
    },
    // 
    editParams() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const {
          data: res
        } = await this.$http.put(
          `/api/private/v1/categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
          { attr_name: this.editForm.attr_name, attr_sel: this.activeName }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }
        this.$message.success('')
        this.getParamsData()
        this.editDialogVisible = false
      })
    },
    // id
    async removeParams(id) {
      const confirmResult = await this.$confirm(
        '',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // 
      const { data: res } = await this.$http.delete(
        `/api/private/v1/categories/${this.cateId}/attributes/${id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getParamsData()
    },
    // enter
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      // return
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      // 
      this.saveAttrValues(row)
    },
    // 
    async saveAttrValues(row) {
      // 
      const { data: res } = await this.$http.put(
        `/api/private/v1/categories/${this.cateId}/attributes/${row.attr_id}`,
        { attr_name: row.attr_name, attr_sel: row.attr_sel, attr_vals: row.attr_vals.join(' ') }
      )

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    },
    showInput(row) {
      row.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    handleClose(i, row) {
      row.attr_vals.splice(i, 1)
      this.saveAttrValues(row);
    }
  },
  computed: {
    // truefalse
    isBtnDisabled() {
      if (this.selectedCateKeys.length !== 3) {
        return true
      } else {
        return false
      }
    },
    // id
    cateId() {
      if (this.selectedCateKeys.length === 3) {
        return this.selectedCateKeys[2]
      }
      return null
    },
    // 
    titleText() {
      if (this.activeName === 'many') {
        return ''
      } else {
        return ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin-right: 20px;
}
.input-new-tag {
  width: 120px;
}
</style>
